<template>
  <div class="bigBox">
    <div class="leftDiv">
      <el-menu style="" class="leftNav" unique-opened>
        <el-submenu
          :index="index + ''"
          v-for="(item, index) in leftNavData"
          :key="index"
        >
          <template slot="title">{{ index }}</template>
          <el-submenu
            :index="index2 + ''"
            v-for="(item2, index2) in item"
            :key="index2"
          >
            <template slot="title">{{ index2 }}</template>
            <el-menu-item
              :index="index2 + item3"
              v-for="(item3, index3) in item2"
              :key="index3"
              @click="NavClick(index, index2, item3)"
              >{{ item3 }}</el-menu-item
            >
          </el-submenu>
        </el-submenu>
      </el-menu>
    </div>
    <div class="rightDiv">
      <div>
        <ul class="rightUl">
          <li
            v-for="(item, index) in re_spu_list"
            :key="index"
            @click="tagLi(item, index)"
            class="allLi"
          >
            {{ item }}
          </li>
        </ul>
      </div>
      <h3>预期月销量</h3>
      <table>
        <thead>
          <tr>
            <th>---</th>
            <th>{{ prevThreeMonth }}</th>
            <th>{{ prevTwoMonth }}</th>
            <th>{{ prevOneMonth }}</th>
            <th>订单比率</th>
            <th>{{ nextMonth }}备货</th>
            <th>
              修正系数
              <el-button type="warning" size="mini" @click="editCoefficient1"
                >编 辑</el-button
              >
            </th>
            <th>预期修正</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{{ spu }}</td>
            <td>{{ re_spu_data.before_month_2 }}</td>
            <td>{{ re_spu_data.before_month_1 }}</td>
            <td>{{ re_spu_data.before_month }}</td>
            <td></td>
            <td>{{ re_spu_data.ready_month }}</td>
            <td>
              <span>{{ re_spu_data.coefficient }}</span>
              <input
                @blur="editChange(re_spu_data)"
                class="editSpu"
                :style="{ zIndex: isEditSpu ? '1' : '-1' }"
                v-model="re_spu_data.coefficient"
                type="text"
              />
            </td>
            <td>{{ re_spu_data.expected_num }}</td>
          </tr>
          <tr v-for="(item, index) in re_sku_data" :key="index">
            <td>{{ item.sku }}</td>
            <td>{{ item.before_month_2 }}</td>
            <td>{{ item.before_month_1 }}</td>
            <td>{{ item.before_month }}</td>
            <td>
              {{
                (
                  (getNum(item.before_month_2) +
                    getNum(item.before_month_1) +
                    getNum(item.before_month)) /
                  (getNum(re_spu_data.before_month_2) +
                    getNum(re_spu_data.before_month_1) +
                    getNum(re_spu_data.before_month))
                ).toFixed(2)
              }}
            </td>
            <td></td>
            <td></td>
            <td></td>
          </tr>
        </tbody>
      </table>

      <h3>发货数量</h3>
      <table>
        <thead>
          <tr>
            <th>spu</th>
            <th>
              库存系数
              <!-- <el-button type="warning" size="mini" @click="editCoefficient2"
                >编辑</el-button
              > -->
            </th>
            <th>安全库存</th>
            <th>下个月所剩库存</th>
            <th>应有库存</th>
            <th>预期销量+</th>
            <th>预期销量</th>
            <th>实际库存</th>
            <th>发货数量</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>{{ spu }}</td>
            <td>
              {{ re_spu_data.coefficient }}
              <!-- <span>{{ re_spu_data.coefficient }}</span>
              <input
                @blur="isEditSpu1 = false"
                @change="editChange1(re_spu_data)"
                class="editSpu"
                :style="{ zIndex: isEditSpu1 ? '1' : '-1' }"
                v-model="re_spu_data.coefficient"
                type="text"
              /> -->
            </td>
            <td>{{ re_spu_data.safety_inventory }}</td>
            <td>{{ re_spu_data.last_month_inventory }}</td>
            <td>{{ re_spu_data.should_inventory }}</td>
            <td>{{ re_spu_data.expected_num }}</td>
            <td>{{ re_spu_data.expected_num }}</td>
            <td>{{ re_spu_data.actual_inventory }}</td>
            <td>{{ re_spu_data.ship_num }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      // isEditSpu1: false,
      isEditSpu: false,
      platformLsit: [], //渠道下拉框
      platform: "", //选择器渠道

      prevOneMonth: "", //上个月
      prevTwoMonth: "", //上2个月
      prevThreeMonth: "", //上3个月
      nextMonth: "", //下个月

      leftNavData: [], //侧边导航栏数据

      firstNav: "", //一级导航
      twiceNav: "", //二级导航
      threeNav: "", //三级导航

      re_spu_list: [], //spu数组

      re_spu_data: [], //月销量表格
      re_sku_data: [], //发货数量表格
      spu: "", //选中的SPU
      startEdit: false
    };
  },
  methods: {
    editChange(item) {
      // 判断是否是数字，如果不是置空

      if (isNaN(item.coefficient)) {
        item.coefficient = "";
        console.log(this.isEditSpu);
        alert("请输入数字类型");
        return;
      }
      console.log(item);
      this.isEditSpu = false;
      let url = this._url2 + "server/stock_management/Inventory_forecast/";
      let fd = new FormData();
      fd.append("id", item.id);
      fd.append("coefficient", item.coefficient);
      this.$axios.post(url, fd).then(res => {
        console.log(res);
        if (res.data.code === 200) {
          alert("提交成功");
          // 刷新数据
          this.reloadData();
        }
      });
    },
    editChange1(item) {
      // 判断是否是数字，如果不是置空
      if (isNaN(item.coefficient)) {
        item.coefficient = "";
        console.log(this.isEditSpu);
        alert("请输入数字类型");
        return;
      }
      console.log(item);
      this.isEditSpu = false;
      let url = this._url2 + "server/stock_management/Inventory_forecast/";
      let fd = new FormData();
      fd.append("id", item.id);
      fd.append("coefficient", item.coefficient);
      this.$axios.post(url, fd).then(res => {
        console.log(res);
        if (res.data.code === 200) {
          alert("提交成功");
          // 刷新数据
          this.reloadData();
        }
      });
    },
    editCoefficient1() {
      if (!this.startEdit) return;
      this.isEditSpu = true;
    },
    editCoefficient2() {
      if (!this.startEdit) return;
      this.isEditSpu1 = true;
    },
    NavClick(index, index2, item3) {
      //点击三级导航栏事件
      this.firstNav = index;
      this.twiceNav = index2;
      this.threeNav = item3;
      axios({
        url: this._url2 + "server/stock_management/Inventory_forecast/",
        method: "get",
        params: {
          site: this.threeNav,
          country: this.twiceNav
        }
      }).then(res => {
        this.re_spu_list = res.data.re_spu_list;

        let allLi = document.querySelectorAll(".allLi");
        for (let i = 0; i < allLi.length; i++) {
          allLi[i].style.background = "";
        }
        this.re_spu_data = [];
        this.re_sku_data = [];
        this.spu = "";
      });
    },
    reloadData() {
      axios
        .get(this._url2 + "server/stock_management/Inventory_forecast/", {
          params: {
            site: this.threeNav,
            country: this.twiceNav,
            spu: this.spu
          }
        })
        .then(res => {
          console.log(res);
          this.re_spu_data = res.data.re_spu_data[this.spu];
          console.log(this.re_spu_data);
          this.re_sku_data = res.data.re_sku_data;
        });
    },
    tagLi(name, index) {
      // console.log(name);

      let allLi = document.querySelectorAll(".allLi");
      for (let i = 0; i < allLi.length; i++) {
        allLi[i].style.background = "";
      }
      allLi[index].style.background = "skyblue";
      axios({
        url: this._url2 + "server/stock_management/Inventory_forecast/",
        method: "get",
        params: {
          site: this.threeNav,
          country: this.twiceNav,
          spu: name
        }
      }).then(res => {
        this.startEdit = true;
        console.log(res);
        this.spu = name;
        this.re_spu_data = res.data.re_spu_data[name];
        console.log(this.re_spu_data);
        this.re_sku_data = res.data.re_sku_data;
      });
    },
    getNum(num) {
      //计算订单比率
      if (!num) {
        num = 0;
      } else {
        num = parseInt(num);
      }
      return num;
    }
  },
  mounted() {
    //获取前三个月
    let myDate = new Date();
    let Y = myDate.getFullYear();
    let M = myDate.getMonth();
    let D = myDate.getDate();
    console.log(D);
    if (D > 15) {
      if (parseInt(M) < 11) {
        this.nextMonth = Y + "-" + (parseInt(M) + 2);
      } else {
        this.nextMonth = parseInt(Y) + 1 + "-" + 1;
      }
      if (parseInt(M) > 1) {
        this.prevOneMonth = Y + "-" + (parseInt(M) + 1);
        this.prevTwoMonth = Y + "-" + parseInt(M);
        this.prevThreeMonth = Y + "-" + (parseInt(M) - 1);
      } else if (parseInt(M) == 1) {
        this.prevOneMonth = Y + "-" + (parseInt(M) + 1);
        this.prevTwoMonth = Y + "-" + parseInt(M);
        this.prevThreeMonth = parseInt(Y) - 1 + "-" + 12;
      } else if (parseInt(M) == 0) {
        this.prevOneMonth = parseInt(Y) + "-" + 1;
        this.prevTwoMonth = parseInt(Y) - 1 + "-" + 12;
        this.prevThreeMonth = parseInt(Y) - 1 + "-" + 11;
      }
    } else {
      if (parseInt(M) < 12) {
        this.nextMonth = Y + "-" + (parseInt(M) + 1);
      } else {
        this.nextMonth = parseInt(Y) + 1 + "-" + 1;
      }
      if (parseInt(M) > 2) {
        this.prevOneMonth = Y + "-" + parseInt(M);
        this.prevTwoMonth = Y + "-" + (parseInt(M) - 1);
        this.prevThreeMonth = Y + "-" + (parseInt(M) - 2);
      } else if (parseInt(M) == 2) {
        this.prevOneMonth = Y + "-" + parseInt(M);
        this.prevTwoMonth = Y + "-" + (parseInt(M) - 1);
        this.prevThreeMonth = parseInt(Y) - 1 + "-" + 12;
      } else if (parseInt(M) == 1) {
        this.prevOneMonth = Y + "-" + parseInt(M);
        this.prevTwoMonth = parseInt(Y) - 1 + "-" + 12;
        this.prevThreeMonth = parseInt(Y) - 1 + "-" + 11;
      } else if (parseInt(M) == 0) {
        this.prevOneMonth = parseInt(Y) - 1 + "-" + 12;
        this.prevTwoMonth = parseInt(Y) - 1 + "-" + 11;
        this.prevThreeMonth = parseInt(Y) - 1 + "-" + 10;
      }
    }
  },
  created() {
    axios({
      //左侧导航栏（权限用）
      url: this._url2 + "server/personnel_management/area_sign2/",
      method: "get"
    }).then(res => {
      console.log(res, 5);
      this.leftNavData = res.data.data;
    });
  }
};
</script>

<style scoped>
.bigBox {
  height: 92vh;
  max-height: 92vh;
  overflow: auto;
  background: rgba(255, 255, 255, 0.9);
}
td {
  position: relative;
}
.editSpu {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.leftDiv {
  width: 12vw;
  height: 100vh;
  position: fixed;
  left: 0;
  background: rgba(255, 255, 255, 0.9);
}
.rightDiv {
  padding-left: 18vw;
}

table {
  /*width: 84vw;*/
  margin-bottom: 15px;
  border-collapse: collapse;
}

tbody {
  display: block;
  max-height: 65vh;
  width: fit-content;
  overflow-y: auto;
}

thead,
tbody tr {
  display: table;
  width: fit-content;
  table-layout: fixed;
}

/*th和td的宽度使用vw或者px*/
thead th,
tbody tr td {
  border: 1px solid #e5e5e5;
  border-top: none;
  text-align: center;
  width: 8vw;
}

table thead tr {
  border-top: 1px solid #e5e5e5;
  background-color: #f7f5f5;
}

table > tbody > tr:hover {
  background: #eaf7e0;
}

table > thead > tr > th {
  font-size: 0.9rem;
  color: #000;
  font-weight: 600;
  height: 5vh;
  line-height: 5vh;
}

table > tbody > tr > td {
  font-size: 0.7rem;
  color: #000;
  height: 5vh;
  line-height: 5vh;
}

/* 设置滚动条的大小 */
::-webkit-scrollbar {
  width: 9px;
  height: 9px;
}

.rightUl {
  display: flex;
  flex-wrap: wrap;
}

.rightUl li {
  width: 20%;
  margin: 5px;
  border: #ddd solid 1px;
  text-align: center;
  cursor: pointer;
}
</style>



